@import "src/utils/variables";
@import "src/utils/mixins";

.block {
  @include flexCenter;
  min-height: 100vh;

  @media (max-width: 970px) {
    display: unset;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (max-width: 970px) {
      display: unset;
    }

    .input {
      width: 95%;

      @media (max-width: 970px) {
        width: 100%;
      }

      .name {
        @include flexCenter();
        justify-content: flex-start;
        font-family: 'Montserrat', sans-serif;
        @include hideTextTable;

        span {
          font-size: xxx-large;
          margin-left: 15px;
          text-decoration: none;
          cursor: pointer;

          &:hover {
            color: $backColorSecond;
          }
        }

        .hHidden {
          margin: 20px 0 20px 10px;
          display: inline-block;
          white-space: nowrap; /* Запрещаем перенос строк */
          overflow: hidden; /* Обрезаем лишнее */
          text-overflow: ellipsis;
          word-break: break-all;
          max-width: 600px;
        }
      }

    }

    .tableWrap {
      overflow-x: auto;
      overflow-y: hidden;
      margin: 0 10px;
    }

    @include tableMain(900px);

  }
}